import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BudgetsHttpService } from '@backbase/data-ang/budgeting';
import { Observable } from 'rxjs';
import { BudgetId, BudgetItem, BudgetList, BudgetPost } from '../model/budgets';

@Injectable()
export class BudgetService {
  constructor(private readonly budgetingDataHttpService: BudgetsHttpService) {}

  getBudgets(): Observable<BudgetList> {
    return this.budgetingDataHttpService.getBudgets();
  }

  createBudget(budget: BudgetPost): Observable<HttpResponse<BudgetId>> {
    return this.budgetingDataHttpService.postBudgets({ budgetsPostRequestBody: budget }, 'response');
  }

  updateBudget(budgetId: string, budget: BudgetItem): Observable<HttpResponse<null>> {
    return this.budgetingDataHttpService.putId({ id: budgetId, idPutRequestBody: budget }, 'response');
  }

  deleteBudget(budgetId: string): Observable<HttpResponse<null>> {
    return this.budgetingDataHttpService.deleteId({ id: budgetId }, 'response');
  }
}
