import { Component, Directive, EventEmitter, Input, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BudgetsGetResponseBody } from '@backbase/data-ang/budgeting';
import { AssetsResolver, AssetsService, BbTemplate } from '@backbase/foundation-ang/core';
import { Observable } from 'rxjs';
import { BaseCurrency, BudgetFrequency, ModalConfig } from '../constants';
import { BudgetData, BudgetList, Currency, Frequency, Period } from '../model/budgets';
import { CategoryItem } from '../model/categories-list';
import { CategoriesService } from '../services/categories.service';
import { UtilityService } from '../services/utility.service';

export interface CreateBudgetForm {
  categoryName: string;
  spendingLimit: Currency;
  frequency: Frequency;
  period: Period;
}

export interface UpdateBudgetForm {
  updateBudgetFormData: {
    spendingLimit: Currency;
  };
  selectedBudgetName: string;
  selectedBudgetId: string;
}

@Component({
  selector: 'bb-budget-manager',
  templateUrl: './budget-manager.component.html',
})
export class BudgetManagerComponent {
  /**
   * Modal header background.
   */
  @Input() background = 'primary';
  /**
   * An array of budgets (objects).
   */
  @Input() budgets: BudgetList | undefined;
  /**
   * A number to define the maximum number of budgets to be shown.
   */
  @Input() maxBudgets: number | undefined;
  /**
   * If set the chart will show a percentage instead of an amount.
   */
  @Input() showPercentage? = false;
  /**
   * Sets the percentage of the safe zone limit of the chart.
   */
  @Input() safeZoneLimit: number | undefined;
  /**
   * EventEmitter for triggering a editBudget event.
   */
  @Output() editBudget = new EventEmitter<UpdateBudgetForm>();
  /**
   * EventEmitter for triggering a createBudget event.
   */
  @Output() createBudget = new EventEmitter<CreateBudgetForm>();
  /**
   * EventEmitter for triggering a deleteBudget event.
   */
  @Output() deleteBudget = new EventEmitter<BudgetData>();

  hostRef = this;

  private readonly resolveAsset: AssetsResolver = this.assetsService.createAssetsResolver({
    widgetName: 'bb-budget-widget-ang',
    getURIFromAssetID: (assetId: string) => `${assetId}`,
  });

  images: Observable<string>[] = ['1', '2'].map((i: string) => this.resolveAsset(i + '-step.svg'));
  themev2img: Observable<string> = this.resolveAsset('modal-header.png');

  readonly baseCurrency = BaseCurrency;
  private readonly budgetFrequency = BudgetFrequency;

  readonly baseBudgetModalOptions = ModalConfig;
  readonly confirmationModalOptions = {
    ...this.baseBudgetModalOptions,
    size: 'md',
  };
  readonly categoriesList: Observable<CategoryItem[]> = this.categoriesService.getCategories();

  selectedBudget: BudgetData | undefined;
  updateBudgetForm: FormGroup | undefined;
  createBudgetForm: FormGroup | undefined;

  discardEditModalActive = false;
  discardCreateModalActive = false;
  deleteBudgetModalActive = false;
  updateBudgetModalActive = false;
  createBudgetModalActive = false;
  activeStep = 1;

  get startOfMonth() {
    const date = new Date();
    const year = date.getFullYear();
    let month = '' + (date.getMonth() + 1);
    month = month.padStart(2, '0');
    return `${year}-${month}-01`;
  }

  get endOfMonth() {
    const date = new Date();
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const newDate = new Date(year, month, 0);
    const day = newDate.getDate();
    const formattedMonth = month.toString().padStart(2, '0');

    return `${year}-${formattedMonth}-${day}`;
  }

  get budgetList(): BudgetList | undefined {
    if (this.maxBudgets && this.budgets) {
      return this.budgets.slice(0, this.maxBudgets);
    }

    return this.budgets;
  }

  constructor(
    private readonly categoriesService: CategoriesService,
    private readonly fb: FormBuilder,
    readonly utility: UtilityService,
    private readonly assetsService: AssetsService,
  ) {}

  private spawnCreateBudgetForm() {
    return this.fb.group({
      categoryName: ['', Validators.required],
      spendingLimit: ['', Validators.required],
      frequency: [this.budgetFrequency, Validators.required],
      period: [this.startOfMonth, Validators.required],
    });
  }

  private spawnUpdateBudgetForm(): FormGroup | undefined {
    if (this.selectedBudget) {
      return this.fb.group({
        spendingLimit: [this.selectedBudget.spendingLimit, Validators.required],
      });
    }
    return undefined;
  }

  closeModal() {
    this.discardEditModalActive = false;
    this.discardCreateModalActive = false;
    this.deleteBudgetModalActive = false;
  }

  processCloseConfirmation() {
    this.closeModal();
    this.updateBudgetModalActive = false;
    this.createBudgetModalActive = false;
    this.activeStep = 1;
  }

  cancelCreateBudget() {
    if (this.createBudgetForm && !this.createBudgetForm.pristine) {
      this.discardCreateModalActive = true;
    } else {
      this.createBudgetModalActive = false;
    }
  }

  cancelUpdateBudget() {
    if (this.updateBudgetForm && !this.updateBudgetForm.pristine) {
      this.discardEditModalActive = true;
    } else {
      this.updateBudgetModalActive = false;
    }
  }

  handleCreate() {
    this.createBudgetModalActive = true;
    this.createBudgetForm = this.spawnCreateBudgetForm();
  }

  submitCreateBudget() {
    if (this.createBudgetForm) {
      this.createBudgetModalActive = false;
      this.activeStep = 1;
      const createBudgetFormValue = {
        ...this.createBudgetForm.value,
        period: {
          startDate: this.createBudgetForm.value.period,
          endDate: this.endOfMonth,
        },
        spendingLimit: {
          currencyCode: this.createBudgetForm.value.spendingLimit.currency,
          amount: this.createBudgetForm.value.spendingLimit.amount,
        },
      };

      this.createBudget.emit(createBudgetFormValue as CreateBudgetForm);
      this.createBudgetForm.reset();
    }
  }

  handleEdit(data: BudgetData) {
    this.updateBudgetModalActive = true;
    this.selectedBudget = data;
    this.updateBudgetForm = this.spawnUpdateBudgetForm();
  }

  submitEditBudget() {
    if (this.selectedBudget && this.updateBudgetForm) {
      this.updateBudgetModalActive = false;
      this.editBudget.emit({
        updateBudgetFormData: {
          ...this.updateBudgetForm.value,
          spendingLimit: {
            currencyCode: this.updateBudgetForm.value.spendingLimit.currency,
            amount: this.updateBudgetForm.value.spendingLimit.amount,
          },
        },
        selectedBudgetName: this.selectedBudget.budgetName,
        selectedBudgetId: this.selectedBudget.id,
      } as UpdateBudgetForm);
      this.selectedBudget = undefined;
    }
  }

  handleDelete(data: BudgetData) {
    this.deleteBudgetModalActive = true;
    this.selectedBudget = data;
  }

  submitDeleteBudget() {
    if (this.selectedBudget) {
      this.deleteBudgetModalActive = false;
      this.deleteBudget.emit(this.selectedBudget);
      this.selectedBudget = undefined;
    }
  }
}

@Directive({
  selector: '[bbBudgetsHeaderCustomizable]',
})
export class BudgetsHeaderCustomizableDirective extends BbTemplate<BudgetManagerComponent, undefined> {}

@Directive({
  selector: '[bbBudgetCardCustomizable]',
})
export class BudgetCardCustomizableDirective extends BbTemplate<
  BudgetManagerComponent,
  BudgetsGetResponseBody | undefined
> {}

@Directive({
  selector: '[bbBudgetConfirmationModalCustomizable]',
})
export class BudgetConfirmationModalCustomizableDirective extends BbTemplate<BudgetManagerComponent, undefined> {}

@Directive({
  selector: '[bbBudgetUpdateModalCustomizable]',
})
export class BudgetUpdateModalCustomizableDirective extends BbTemplate<BudgetManagerComponent, undefined> {}

@Directive({
  selector: '[bbBudgetCreateModalCustomizable]',
})
export class BudgetCreateModalCustomizableDirective extends BbTemplate<BudgetManagerComponent, undefined> {}
