import { HttpResponse } from '@angular/common/http';
import { Category } from '@backbase/data-ang/categories-management';
import { Customizable } from '@backbase/product-summary-common-ang';

export interface CategoryItem extends Customizable {
  categoryId: number;
  categoryName: string;
}

export function categoriesManagementMap(item: Category): CategoryItem {
  return { ...item };
}

export function fromCategoriesManagementResponse(response: HttpResponse<Category[]>): CategoryItem[] {
  return (response.body || []).map(categoriesManagementMap);
}
