import { Injectable } from '@angular/core';
import { CategoriesHttpService } from '@backbase/data-ang/categories-management';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { CategoryItem, fromCategoriesManagementResponse } from '../model/categories-list';

@Injectable()
export class CategoriesService {
  constructor(private readonly categoriesManagementDataHttpService: CategoriesHttpService) {}

  getCategories(): Observable<CategoryItem[]> {
    return this.categoriesManagementDataHttpService
      .getCategories(
        {
          level: 'PARENTS',
        },
        'response',
      )
      .pipe(map((response) => fromCategoriesManagementResponse(response)));
  }
}
