import { HttpErrorResponse } from '@angular/common/http';
import { Component, Directive, Input, Optional, TemplateRef, ViewChild } from '@angular/core';
import { BbTemplate, ItemModel } from '@backbase/foundation-ang/core';
import { Property } from '@backbase/foundation-ang/web-sdk';
import { NotificationService } from '@backbase/ui-ang/notification';
import { BehaviorSubject, combineLatest, ConnectableObservable, Observable, of } from 'rxjs';
import { catchError, flatMap, map, publish, switchMap, withLatestFrom } from 'rxjs/operators';
import { CreateBudgetForm, UpdateBudgetForm } from './budget-manager/budget-manager.component';
import { ModalConfig, Properties, PROPERTIES } from './constants';
import { BudgetData, BudgetItem, BudgetList, BudgetPost } from './model/budgets';
import { BudgetService } from './services/budget.service';
import { UtilityService } from './services/utility.service';

type NotificationHeader = string | TemplateRef<any>;
type NotificationMessage = string | TemplateRef<any>;
enum NotificationModifier {
  SUCCESS = 'success',
  INFO = 'info',
  WARNING = 'warning',
  ERROR = 'error',
}

interface Notification {
  header: NotificationHeader;
  message: NotificationMessage;
  modifier: NotificationModifier | undefined;
}

const filterInputParameters = <T>(input: Observable<T | undefined>, prop: Observable<T>): Observable<T> =>
  combineLatest([input, prop]).pipe(map(([inputVal, propVal]) => (inputVal !== undefined ? inputVal : propVal)));

@Component({
  selector: 'bb-budget-widget',
  templateUrl: './budget-widget.component.html',
})
export class BudgetWidgetComponent {
  @Input('showPercentage') set showPercentageValue(value: boolean) {
    this.showPercentageSubject.next(value);
  }
  @Input('budgetSafeZoneLimit') set budgetSafeZoneLimitValue(value: number) {
    this.budgetSafeZoneLimitSubject.next(value);
  }
  @Input('notificationDismissTime') set notificationDismissTimeValue(value: number) {
    this.notificationDismissTimeSubject.next(value);
  }
  @Input('maxBudgets') set maxBudgetsValue(value: number) {
    this.maxBudgetsSubject.next(value);
  }

  @ViewChild('notificationCreate') notificationCreateTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationUpdate') notificationUpdateTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationDelete') notificationDeleteTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationErrorCreate') notificationErrorCreateTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationErrorUpdate') notificationErrorUpdateTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationErrorDelete') notificationErrorDeleteTemplate: TemplateRef<any> | undefined;

  private readonly showPercentageSubject = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly budgetSafeZoneLimitSubject = new BehaviorSubject<number | undefined>(undefined);
  private readonly notificationDismissTimeSubject = new BehaviorSubject<number | undefined>(undefined);
  private readonly maxBudgetsSubject = new BehaviorSubject<number | undefined>(undefined);

  private readonly reload = new BehaviorSubject(undefined);
  readonly baseBudgetModalOptions = ModalConfig;
  readonly confirmationModalOptions = {
    ...this.baseBudgetModalOptions,
    size: 'sm',
  };
  readonly budgetList: Observable<BudgetList | undefined> = this.reload.asObservable().pipe(
    switchMap(() => this.service.getBudgets()),
    catchError((error: HttpErrorResponse | Error) => {
      this.handleError(error);
      return of(undefined);
    }),
  );

  hostRef = this;
  error: string | undefined;

  constructor(
    private readonly utility: UtilityService,
    private readonly service: BudgetService,
    private readonly notificationService: NotificationService,
    @Optional() private readonly model: ItemModel,
  ) {}

  private pushNotification(
    header: NotificationHeader,
    message: NotificationMessage,
    modifier: NotificationModifier,
    notificationDismissTime: number | undefined,
  ) {
    if (notificationDismissTime) {
      this.notificationService.showNotification({
        header,
        message,
        modifier,
        dismissible: true,
        ttl: notificationDismissTime * 1000,
      });
    }
  }

  private readonly serviceResponse =
    <R>(successHeader: NotificationHeader, errorHeader: NotificationHeader) =>
    (source: Observable<R>): Observable<Notification | undefined> =>
      source.pipe(
        flatMap(() => {
          this.reload.next(undefined);
          return of({ header: successHeader, message: '', modifier: NotificationModifier.SUCCESS });
        }),
        catchError(() => {
          return of({ header: errorHeader, message: '', modifier: NotificationModifier.WARNING });
        }),
        withLatestFrom(this.notificationDismissTime),
        map(([notification, notificationDismissTime]) => {
          if (notification) {
            this.pushNotification(
              notification.header,
              notification.message,
              notification.modifier,
              notificationDismissTime,
            );
          }
          return undefined;
        }),
        publish(),
      );

  createBudget(createBudgetFormData: CreateBudgetForm) {
    const budgetName = createBudgetFormData.categoryName;
    const budgetIcon = this.utility.getCategoryIconClass(createBudgetFormData.categoryName);
    const buildCreateBudgetObj: BudgetPost = Object.assign(createBudgetFormData, {
      budgetName,
      budgetIcon,
    });

    (<ConnectableObservable<Notification>>(
      this.service
        .createBudget(buildCreateBudgetObj)
        .pipe(
          this.serviceResponse(
            this.notificationCreateTemplate as TemplateRef<any>,
            this.notificationErrorCreateTemplate as TemplateRef<any>,
          ),
        )
    )).connect();
  }

  updateBudget(data: UpdateBudgetForm) {
    const buildUpdateBudgetObj: BudgetItem = Object.assign(data.updateBudgetFormData, {
      budgetName: data.selectedBudgetName,
    });

    (<ConnectableObservable<Notification>>(
      this.service
        .updateBudget(data.selectedBudgetId, buildUpdateBudgetObj)
        .pipe(
          this.serviceResponse(
            this.notificationUpdateTemplate as TemplateRef<any>,
            this.notificationErrorUpdateTemplate as TemplateRef<any>,
          ),
        )
    )).connect();
  }

  deleteBudget(budget: BudgetData) {
    (<ConnectableObservable<Notification>>(
      this.service
        .deleteBudget(budget.id)
        .pipe(
          this.serviceResponse(
            this.notificationDeleteTemplate as TemplateRef<any>,
            this.notificationErrorDeleteTemplate as TemplateRef<any>,
          ),
        )
    )).connect();
  }

  handleError(error: HttpErrorResponse | Error): void {
    if (error instanceof HttpErrorResponse) {
      this.error = error.statusText;
    } else if (error instanceof Error) {
      this.error = error.message;
    }
  }

  readonly showPercentage: Observable<boolean> = filterInputParameters(
    this.showPercentageSubject.asObservable(),
    this.getProperty(PROPERTIES.showPercentage).pipe(map((propertyValue) => propertyValue + '' === 'true')),
  );
  readonly budgetSafeZoneLimit: Observable<number> = filterInputParameters(
    this.budgetSafeZoneLimitSubject.asObservable(),
    this.getProperty(PROPERTIES.budgetSafeZoneLimit).pipe(map((propertyValue) => parseInt(<string>propertyValue, 10))),
  );
  readonly notificationDismissTime: Observable<number> = filterInputParameters(
    this.notificationDismissTimeSubject.asObservable(),
    this.getProperty(PROPERTIES.notificationDismissTime).pipe(
      map((propertyValue) => parseFloat(<string>propertyValue)),
    ),
  );
  readonly maxBudgets: Observable<number> = filterInputParameters(
    this.maxBudgetsSubject.asObservable(),
    this.getProperty(PROPERTIES.maxBudgets).pipe(map((propertyValue) => parseFloat(<string>propertyValue))),
  );

  private getProperty(property: Properties): Observable<Property> {
    return this.model ? this.model.property(property.name, property.value) : of(property.value);
  }
}

@Directive({
  selector: '[bbBudgetNotificationCustomizable]',
})
export class BudgetNotificationCustomizableDirective extends BbTemplate<BudgetWidgetComponent, undefined> {}
